@import "https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
*, :before, :after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 16px;
}

img {
  max-width: 100%;
  max-height: 100%;
  display: block;
}

.container {
  width: 100%;
  max-width: 1094px;
  margin: 0 auto;
  padding: 0 16px;
  display: block;
}

.link {
  color: inherit;
  white-space: nowrap;
  text-decoration: none;
  transition: color .2s;
}

.link:hover {
  color: #8fed8f;
}

.btn {
  color: #fff;
  font-family: inherit;
  font-size: inherit;
  border: none;
  border-radius: 6px;
  padding: 16px 25px;
  display: inline-block;
}

.btn_green {
  background-color: #42ae60;
  transition-property: color, background-color;
  transition-duration: .2s;
  transition-timing-function: ease;
}

.btn_green:hover {
  color: #fff;
  background-color: #87bd1d;
}

.header {
  color: #fff;
  background-color: #43513d;
  padding: 25px 0;
  overflow-x: hidden;
}

.header__wrapper {
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas: ". header__nav header__btns";
  align-items: center;
  display: grid;
}

.header__nav {
  grid-area: header__nav;
  gap: 48px;
  display: flex;
}

.header__btns {
  grid-area: header__btns;
  justify-content: flex-end;
  margin-left: 40px;
  display: flex;
}

.intro {
  color: #fff;
  background-color: #273720;
  padding: 80px 0;
  overflow-x: hidden;
}

.intro__wrapper {
  flex-direction: column;
  gap: 180px;
  display: flex;
}

.intro__left-and-right {
  grid-template-columns: 450px auto;
  display: grid;
}

.intro__title {
  margin: 0;
  font-size: 72px;
  font-weight: normal;
  line-height: 1;
}

.intro__description {
  margin: 0;
  font-size: 19px;
}

.intro__left {
  flex-direction: column;
  align-items: flex-start;
  gap: 20px;
  display: flex;
}

.intro__right {
  transform: translateX(100px);
}

.intro__img {
  object-fit: cover;
  border-radius: 10px;
  height: 100%;
}

.intro__companies {
  justify-content: space-between;
  gap: 20px;
  display: flex;
}

.intro__companies-text {
  width: 200px;
  margin: 0;
}

.intro__companies-images {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.section-header {
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 25px;
  display: flex;
}

.section-header__suptitle {
  text-transform: uppercase;
  font-size: 15px;
}

.section-header__title {
  margin: 0;
  font-size: 71px;
  font-weight: normal;
  line-height: 1;
}

.section-header__subtitle {
  font-size: 17px;
}

.lines-text {
  font-size: 31px;
}

.lines-text__item {
  border-top: 1px solid #000;
  padding: 25px 0;
}

.lines-text__item:last-child {
  border-bottom: 1px solid #000;
}

.deals {
  padding: 80px 0;
  overflow-x: hidden;
}

.deals__section-header {
  color: #273720;
}

.deals__two {
  grid-template-columns: 470px auto;
  justify-content: space-between;
  align-items: center;
  gap: 80px;
  margin-top: 76px;
  display: grid;
}

.vendor {
  padding: 80px 0;
  overflow-x: hidden;
}

.vendor__section-header {
  color: #273720;
}

.vendor__two {
  grid-template-columns: 470px auto;
  justify-content: space-between;
  align-items: center;
  gap: 40px;
  margin-top: 140px;
  display: grid;
}

.vendor__two-right {
  position: relative;
}

.vendors__two-right-img_absolute {
  position: absolute;
  top: -28%;
  left: -7%;
}

.vendors__two-right-line {
  clip-path: polygon(7% 100%, 6% 66%, 42% 0%, 66% 1%, 64% 38%, 31% 100%);
  background-color: #e6ff4c;
  width: 150%;
  height: 174%;
  position: absolute;
  bottom: 13%;
  left: 30%;
}

.teams {
  color: #fff;
  background-color: #273720;
  padding: 80px 0;
  overflow-x: hidden;
}

.teams__slider {
  width: 100%;
}

.teams__wrapper {
  flex-direction: column;
  gap: 60px;
  display: flex;
}

.teams__slider-arrows {
  justify-content: center;
  gap: 48px;
  margin-top: 48px;
  display: flex;
}

.teams__slider-wrapper {
  touch-action: pan-y;
  align-items: center;
  display: flex;
}

.teams__slider-item {
  --width: min(1000px, max(75vw, 250px));
  min-width: var(--width);
  max-width: var(--width);
  border-radius: 40px;
  flex-direction: column;
  align-items: center;
  padding: 44px 82px;
  font-size: 32px;
  display: flex;
  overflow: hidden;
}

.teams__slider-item_odd {
  color: #273720;
  background-color: #d2ebd5;
}

.teams__slider-item_even {
  color: #273720;
  background-color: #fff;
}

.teams__slider-review {
  align-items: center;
  gap: 24px;
  display: flex;
}

.teams__slider-review-img {
  --size: 64px;
  min-width: var(--size);
  max-width: var(--size);
  height: var(--size);
}

.teams__slider-review-text {
  font-size: 18px;
}

.teams__slider-arrow {
  cursor: pointer;
  padding: 20px;
  transition: background-color .2s;
}

.teams__slider-arrow:hover {
  background-color: #ffffff1a;
}

.security {
  color: #fff;
  background-color: #273720;
  padding: 120px 0;
  overflow: hidden visible;
}

.security__wrapper {
  position: relative;
}

.security__img {
  z-index: 1;
  pointer-events: none;
  -webkit-user-select: none;
  user-select: none;
  width: 230px;
  max-width: none;
  max-height: none;
  position: absolute;
  top: -10%;
  left: 0;
}

.security__title {
  margin-bottom: 20px;
}

.footer {
  color: #fff;
  background-color: #273720;
  padding: 80px 0;
}

.footer__wrapper {
  justify-content: space-between;
  display: flex;
}

.footer__logo-wrapper {
  min-width: max-content;
}

.footer__columns {
  gap: 64px;
  display: flex;
}

.footer__column {
  flex-direction: column;
  gap: 16px;
  display: flex;
}

.footer__column-item_head {
  font-size: 32px;
}

.footer__column-item_logos {
  gap: 24px;
  display: flex;
}

@media (width <= 1000px) {
  .intro__companies {
    flex-direction: column;
    align-items: center;
  }

  .deals__two {
    grid-template-columns: auto;
    place-content: center;
  }

  .vendor__two {
    grid-template-columns: auto;
    place-content: center;
    gap: 160px;
    margin-top: 76px;
  }

  .lines-text__item {
    font-size: 20px;
  }

  .section-header__title {
    font-size: 40px;
  }

  .security__img {
    width: 150px;
  }
}

@media (width <= 720px) {
  .header__wrapper {
    grid-template-columns: auto;
    grid-template-areas: "header__nav"
                         "header__btns";
    gap: 32px;
  }

  .header__btns {
    flex-direction: column;
    align-items: center;
    margin-left: 0;
  }

  .header__nav {
    flex-direction: column;
    align-items: center;
    gap: 32px;
  }

  .intro__wrapper {
    gap: 80px;
  }

  .intro__left-and-right {
    grid-template-columns: auto;
    place-items: center;
    gap: 64px;
  }

  .intro__left {
    width: 100%;
    max-width: 400px;
  }

  .intro__title {
    font-size: 48px;
  }

  .intro__description {
    font-size: 16px;
  }

  .intro__right {
    transform: none;
  }

  .teams__slider-item {
    padding: 16px 32px;
  }

  .teams__slider-item-text {
    font-size: 16px;
  }

  .teams__slider-review-img {
    --size: 50px;
  }

  .teams__slider-review-text {
    font-size: 13px;
  }
}

@media (width <= 680px) {
  .footer__wrapper {
    flex-direction: column;
    align-items: center;
    gap: 64px;
  }

  .footer__columns {
    flex-wrap: wrap;
    justify-content: center;
  }
}
